@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200;300;400;500;600;700;800;900&display=swap");
.html-editor {
  font-family: "Inter", sans-serif;
  background-color: #ffffff;
  position: relative;
  max-width: 755px;
  width: 100%;
  padding: 0 10px;
  min-height: 18rem;
  border: 1px solid #AFB2C3;
  border-radius: 10px;
}
.html-editor.full-screen {
  max-width: 100%;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #ffffff;
  z-index: 99999;
  margin: 0;
}
.html-editor.full-screen .toolbar {
  top: 0;
}
.html-editor.full-screen .editor-view {
  overflow-y: auto;
}
.html-editor .toolbar-bottom {
  z-index: 20;
  background-color: #ffffff;
  border-top: 1px solid #AFB2C3;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin: 0 -10px;
  padding: 7px 10px 10px;
  display: flex;
  justify-content: space-between;
  color: #5C5C85;
  position: sticky;
  bottom: -3px;
}
.html-editor .toolbar-bottom .toolbar-tags span {
  display: inline-block;
  padding: 3px;
  border: 1px solid transparent;
  position: relative;
  cursor: pointer;
  border-radius: 4px;
  transition: 0.3s ease-in-out all;
  line-height: 1;
}
.html-editor .toolbar-bottom .toolbar-tags span:hover {
  border-color: #AFB2C3;
  background-color: #F3F3F7;
}
.html-editor .toolbar-bottom .toolbar-tags span:not(:last-child) {
  margin-right: 12px;
}
.html-editor .toolbar-bottom .toolbar-tags span:not(:last-child):after {
  content: ">";
  position: absolute;
  right: -12px;
}
.html-editor .toolbar {
  position: sticky;
  top: 0;
  z-index: 20;
  background-color: #ffffff;
  border-bottom: 1px solid #AFB2C3;
}
.html-editor .toolbar .toolbar-row {
  display: flex;
  flex-wrap: wrap;
  padding: 10px 0;
  border-bottom: 1px solid #e2e2e2;
  margin-bottom: -3px;
}
.html-editor .toolbar .toolbar-row:last-child {
  border-bottom: none;
}
.html-editor .toolbar .toolbar-row .toolbar-box {
  display: flex;
  margin-bottom: 3px;
}
.html-editor .toolbar .toolbar-row .toolbar-box:not(:last-child) {
  margin-right: 5px;
  padding-right: 5px;
  border-right: 1px solid #AFB2C3;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s ease all;
  font-size: 13px;
  color: #5C5C85;
  min-width: 26px;
  height: 26px;
  border-radius: 6px;
  border: 1px solid transparent;
  cursor: pointer;
  position: relative;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn + .editor-btn {
  margin-left: 2px;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn:hover, .html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.active {
  background-color: #ffffff;
  border-color: #FCFDFF;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.icon-unlink.active {
  background-color: unset;
  border-color: transparent;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.icon-unlink.active:hover {
  background-color: #ffffff;
  border-color: #FCFDFF;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.disabled {
  color: #B3B3C3;
  pointer-events: none;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu {
  position: relative;
  padding: 0 6px;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu::after {
  content: "\e901";
  font-family: "icomoon" !important;
  font-size: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 6px;
  height: 6px;
  margin-left: 7px;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  transition: 0.3s ease-in-out all;
  top: calc(100% + 5px);
  left: -2px;
  z-index: 10;
  width: max-content;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu .editor-btn {
  min-height: 26px;
  height: auto;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu:hover {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu h1,
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu h2,
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu h3,
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu p {
  color: #262D41;
  margin-bottom: 0;
  width: 100%;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu h1 {
  font-size: 20px;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu h2 {
  font-size: 18px;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu h3 {
  font-size: 16px;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu .submenu p {
  font-size: 15px;
}
.html-editor .toolbar .toolbar-row .toolbar-box .editor-btn.has-submenu:hover .submenu {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.html-editor .toolbar .toolbar-row .toolbar-box .toolbar-items {
  display: flex;
  background-color: #F3F3F7;
  border-radius: 6px;
  padding: 2px;
  position: relative;
}
.html-editor .toolbar .toolbar-row .toolbar-box .toolbar-items.format {
  display: block;
}
.html-editor .toolbar .toolbar-row .toolbar-box .toolbar-items.format .editor-btn {
  display: block;
  min-height: auto !important;
  padding: 0 5px;
  margin: 0;
}
.html-editor .toolbar .toolbar-row .toolbar-box .toolbar-items.palette, .html-editor .toolbar .toolbar-row .toolbar-box .toolbar-items.table-box {
  box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.05);
  background-color: #ffffff;
  border: 1px solid #F3F3F7;
}
.html-editor .toolbar .toolbar-row .toolbar-box .toolbar-items.table-box {
  padding: 10px;
  display: flex;
  flex-direction: column;
}
.html-editor .toolbar .toolbar-row .toolbar-box .subbuttons {
  position: relative;
}
.html-editor .toolbar .toolbar-row .toolbar-box .subbuttons .submenu {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  transition: 0.3s ease-in-out all;
  top: calc(100% + 4px);
  left: 0;
  z-index: 10;
  width: max-content;
}
.html-editor .toolbar .toolbar-row .toolbar-box .subbuttons .submenu:hover {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.html-editor .toolbar .toolbar-row .toolbar-box .subbuttons:hover .submenu {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.html-editor .editor-content-box {
  line-height: 1.5;
  margin: 10px 0;
}
.html-editor .editor-content-box .editor-view {
  outline: none;
  min-height: 12rem;
  font-family: "Inter", sans-serif;
  color: #262D41;
}
.html-editor .editor-content-box .editor-view * {
  margin: 0;
}
.html-editor .editor-content-box .editor-view table {
  border-collapse: collapse;
  width: 100%;
  table-layout: fixed;
}
.html-editor .editor-content-box .editor-view table td {
  padding: 5px;
  border: 1px solid #000000;
}
.html-editor .editor-content-box .editor-view h1,
.html-editor .editor-content-box .editor-view h2,
.html-editor .editor-content-box .editor-view h3,
.html-editor .editor-content-box .editor-view p,
.html-editor .editor-content-box .editor-view a,
.html-editor .editor-content-box .editor-view blockquote,
.html-editor .editor-content-box .editor-view ul,
.html-editor .editor-content-box .editor-view ol,
.html-editor .editor-content-box .editor-view table {
  margin: 15px 0;
}
.html-editor .editor-content-box .editor-view h1:first-child,
.html-editor .editor-content-box .editor-view h2:first-child,
.html-editor .editor-content-box .editor-view h3:first-child,
.html-editor .editor-content-box .editor-view p:first-child,
.html-editor .editor-content-box .editor-view a:first-child,
.html-editor .editor-content-box .editor-view blockquote:first-child,
.html-editor .editor-content-box .editor-view ul:first-child,
.html-editor .editor-content-box .editor-view ol:first-child,
.html-editor .editor-content-box .editor-view table:first-child {
  margin-top: 0;
}
.html-editor .editor-content-box .editor-view h1:last-child,
.html-editor .editor-content-box .editor-view h2:last-child,
.html-editor .editor-content-box .editor-view h3:last-child,
.html-editor .editor-content-box .editor-view p:last-child,
.html-editor .editor-content-box .editor-view a:last-child,
.html-editor .editor-content-box .editor-view blockquote:last-child,
.html-editor .editor-content-box .editor-view ul:last-child,
.html-editor .editor-content-box .editor-view ol:last-child,
.html-editor .editor-content-box .editor-view table:last-child {
  margin-bottom: 0;
}
.html-editor .editor-content-box .editor-view u i {
  text-decoration: underline;
}
.html-editor .editor-content-box .editor-view hr {
  border-color: #AFB2C3;
  margin: 20px 0;
}
.html-editor .editor-content-box .editor-view hr:first-child {
  margin-top: 0;
}
.html-editor .editor-content-box .editor-view hr:last-child {
  margin-bottom: 0;
}
.html-editor .editor-content-box .editor-view h1,
.html-editor .editor-content-box .editor-view h2,
.html-editor .editor-content-box .editor-view h3 {
  line-height: 130%;
}
.html-editor .editor-content-box .editor-view h1 {
  font-size: 24px;
}
.html-editor .editor-content-box .editor-view h2 {
  font-size: 20px;
}
.html-editor .editor-content-box .editor-view h3 {
  font-size: 18px;
}
.html-editor .editor-content-box .editor-view strong,
.html-editor .editor-content-box .editor-view b {
  font-weight: 700;
}
.html-editor .editor-content-box .editor-view ol,
.html-editor .editor-content-box .editor-view ul {
  padding: 0;
  list-style-position: outside;
  margin-left: 20px;
}
.html-editor .editor-content-box .editor-view ul {
  list-style-type: disc;
}
.html-editor .editor-content-box .editor-view ol {
  list-style-type: decimal;
}
.html-editor .editor-content-box .editor-view img {
  border-radius: 7px;
}
.html-editor .editor-content-box .editor-view a {
  display: inline;
  color: #0072FF;
}
.html-editor .editor-content-box .editor-view p,
.html-editor .editor-content-box .editor-view a,
.html-editor .editor-content-box .editor-view blockquote,
.html-editor .editor-content-box .editor-view ul,
.html-editor .editor-content-box .editor-view ol {
  font-size: 15px;
  line-height: 150%;
}
.html-editor .editor-content-box .editor-view blockquote {
  font-family: "Source Code Pro", monospace;
  font-style: normal;
  font-size: 12px;
  padding: 15px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #F3F3F7;
}
.html-editor .editor-content-box .editor-view blockquote i {
  font-style: inherit;
  color: #5BDB1F;
  display: inline-block;
}
.html-editor .editor-content-box .editor-view blockquote p {
  font-size: inherit;
}
.html-editor .editor-content-box .editor-view blockquote p + * {
  margin-top: 5px;
}
.html-editor .editor-content-box .editor-view img {
  max-width: 100%;
  touch-action: none;
  cursor: pointer;
  position: relative;
  border: 4px solid transparent;
  transition: 0.2s ease-in-out all;
}
.html-editor .editor-content-box .editor-view img:focus {
  border-color: #0072FF;
}
.html-editor .editor-content-box .html-view {
  outline: none;
  display: none;
  width: 100%;
  height: 200px;
  border: none;
  resize: none;
}

/* Palette */
.palette__line {
  position: absolute;
  height: 2px;
  width: 13px;
  left: 6px;
  bottom: 5px;
  background-color: #5C5C85;
}
.palette__grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 3px;
  cursor: auto;
}
.palette__item {
  position: relative;
  width: 34px;
  height: 34px;
  transition: 0.3s ease-in-out all;
  border-radius: 4px;
  border: 2px solid transparent;
  cursor: pointer;
}
.palette__item img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.palette__item.active {
  border-color: #262D41;
}
.palette__item:hover {
  border-color: #262D41;
}
.palette__item.color-1 {
  background-color: #BFEDD1;
}
.palette__item.color-2 {
  background-color: #FBEEB9;
}
.palette__item.color-3 {
  background-color: #F7CAC5;
}
.palette__item.color-4 {
  background-color: #ECCBFA;
}
.palette__item.color-5 {
  background-color: #C1E0F4;
}
.palette__item.color-6 {
  background-color: #2EC26C;
}
.palette__item.color-7 {
  background-color: #F1C40F;
}
.palette__item.color-8 {
  background-color: #DF3E2C;
}
.palette__item.color-9 {
  background-color: #B86AD8;
}
.palette__item.color-10 {
  background-color: #3598DB;
}
.palette__item.color-11 {
  background-color: #159179;
}
.palette__item.color-12 {
  background-color: #E67F23;
}
.palette__item.color-13 {
  background-color: #BB372A;
}
.palette__item.color-14 {
  background-color: #853FA1;
}
.palette__item.color-15 {
  background-color: #236EA1;
}
.palette__item.color-16 {
  background-color: #ECF0F1;
}
.palette__item.color-17 {
  background-color: #CFD4DA;
}
.palette__item.color-18 {
  background-color: #95A5A5;
}
.palette__item.color-19 {
  background-color: #7E8C8D;
}
.palette__item.color-20 {
  background-color: #34495E;
}
.palette__item.color-21 {
  background-color: #000000;
}
.palette__item.color-reset {
  background-color: unset;
  border: 2px solid #F3F3F7;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.palette__item.color-reset:before {
  content: "";
  position: absolute;
  width: 32px;
  height: 2px;
  background: #ED4234;
  transform: rotate(-45deg);
}
.palette__item.color-reset.active {
  border-color: #262D41;
}
.palette__item.color-reset:hover {
  border-color: #262D41;
}
.palette__item.color-select {
  grid-column: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  background-color: #F9F9FA;
  border: 2px solid #F3F3F7;
  position: relative;
}
.palette__item.color-select.active {
  border-color: #262D41;
}
.palette__item.color-select:hover {
  border-color: #262D41;
}
.palette__item.color-select:before {
  content: "\e92d";
  font-family: "icomoon" !important;
}
.palette__item.color-select input {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

/* Table grid */
.table tbody {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.table tr {
  display: flex;
  gap: 2px;
}
.table td {
  width: 16px;
  height: 16px;
  border-radius: 2px;
  border: 1px solid #AFB2C3;
  background-color: #ffffff;
  position: relative;
}
.table td:hover, .table td.selected {
  border-color: #0072FF;
  background-color: #BCDEFB;
}
.table__size {
  font-family: "Inter", sans-serif !important;
  margin-top: 10px;
  font-size: 15px;
  text-align: center;
}

/* Modal */
.modal {
  z-index: 9999;
  display: none;
  font-family: "Inter", sans-serif;
}
.modal.link {
  display: block;
}
.modal.link .modal-wrapper.link {
  display: block;
}
.modal.symbols {
  display: block;
}
.modal.symbols .modal-wrapper.symbols {
  display: block;
}
.modal .modal-wrapper {
  background-color: #FFF;
  padding: 40px 25px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 400px;
  width: 100%;
  min-height: 10rem;
  z-index: 120;
  display: none;
}
.modal .modal-wrapper.symbols {
  max-width: 590px;
  padding: 25px;
}
.modal .modal-wrapper.symbols .modal-content h2 {
  margin-bottom: 30px;
}
.modal .modal-wrapper .modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 34px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 4px;
  color: #AFB2C3;
  background-color: #F3F3F7;
  z-index: 20;
  font-family: "icomoon" !important;
  border: 1px solid transparent;
  transition: 0.3s ease-in-out all;
}
.modal .modal-wrapper .modal-close:hover {
  border-color: #0072FF;
}
.modal .modal-wrapper .modal-close:before {
  content: "\e907";
  font-size: 13px;
}
.modal .modal-wrapper .input::placeholder {
  color: #AFB2C3;
}
.modal .modal-wrapper .input input:-webkit-autofill,
.modal .modal-wrapper .input input:-webkit-autofill:hover,
.modal .modal-wrapper .input input:-webkit-autofill:focus,
.modal .modal-wrapper .input input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 40px #FCFDFF inset !important;
  -webkit-text-fill-color: #9CA8BA !important;
}
.modal .modal-wrapper .input:focus {
  border-color: #54A0FF;
}
.modal .modal-wrapper .modal-content {
  border: 0;
  flex-direction: column;
}
.modal .modal-wrapper .modal-content form {
  width: 100%;
}
.modal .modal-wrapper .modal-content h2 {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 20px;
  color: #262D41;
}
.modal .modal-wrapper .modal-content .modal-search {
  background-color: #ffffff;
  position: relative;
  color: #9CA8BA;
  width: 100%;
  display: flex;
  align-items: center;
}
.modal .modal-wrapper .modal-content .modal-search span {
  font-size: 20px;
  position: absolute;
  left: 10px;
  cursor: pointer;
}
.modal .modal-wrapper .modal-content .modal-search input {
  border: 1px solid #F3F3F7;
  border-radius: 7px;
  padding: 10px 10px 10px 40px;
  font-size: 15px;
  font-family: "Inter", sans-serif;
  color: #9CA8BA;
  width: 100%;
  background-color: inherit;
  transition: 0.3s ease-in-out all;
}
.modal .modal-wrapper .modal-content .modal-search input:focus {
  border-color: #54A0FF;
}
.modal .modal-wrapper .modal-content .modal-input {
  padding: 14px;
  font-size: 15px;
  line-height: 1;
  border-radius: 7px;
  border: 1px solid #AFB2C3;
  transition: 0.3s ease-in-out all;
}
.modal .modal-wrapper .modal-content .checkbox {
  margin: 30px 0;
}
.modal .modal-wrapper .modal-content button {
  background-color: #0ABA5D;
  border: none;
  border-radius: 7px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  padding: 13px 18px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 100%;
}
.modal .modal-wrapper .modal-content button:hover:after {
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 40%, rgba(255, 255, 255, 0.7) 100%);
  animation: gradient 0.3s linear forwards;
}
.modal .modal-wrapper .modal-content button:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform: skew(45deg);
}
.modal .modal-wrapper .modal-inner {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-column-gap: 20px;
  grid-row-gap: 25px;
}
.modal .modal-wrapper .modal-inner button {
  grid-column: 2;
}
.modal .modal-wrapper .tab {
  padding: 7px 6px;
  font-size: 15px;
  border-radius: 4px;
  transition: 0.3s ease-in-out all;
  line-height: 1;
  cursor: pointer;
  color: #262D41;
}
.modal .modal-wrapper .tab:not(:last-child) {
  margin-bottom: 2px;
}
.modal .modal-wrapper .tab:hover {
  color: #0072FF;
  background-color: #DCEBFF;
}
.modal .modal-wrapper .tab.active {
  color: #0072FF;
  background-color: #DCEBFF;
}
.modal .modal-wrapper .tab-content {
  display: none;
}
.modal .modal-wrapper .tab-content.active {
  display: block;
}
.modal .modal-wrapper .tab-content__symbols {
  display: grid;
  grid-template-columns: repeat(auto-fit, 26px);
  grid-row-gap: 2px;
  grid-auto-rows: max-content;
  padding: 13px;
  margin-top: 5px;
  background-color: #ffffff;
  color: #5C5C85;
  border: 1px solid #F3F3F7;
  border-radius: 7px;
  overflow-y: auto;
  height: 230px;
  scrollbar-width: thin;
  scrollbar-color: #0ABA5D transparent;
}
.modal .modal-wrapper .tab-content__symbols::-webkit-scrollbar {
  width: 4px;
}
.modal .modal-wrapper .tab-content__symbols::-webkit-scrollbar-track {
  background: transparent;
}
.modal .modal-wrapper .tab-content__symbols::-webkit-scrollbar-thumb {
  background-color: #0ABA5D;
  border-radius: 4px;
}
.modal .modal-wrapper .tab-content__symbols::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.modal .modal-wrapper .tab-content__symbols span {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  width: 26px;
  height: 26px;
  border: 1px solid transparent;
  transition: 0.3s ease-in-out all;
  border-radius: 4px;
  cursor: pointer;
}
.modal .modal-wrapper .tab-content__symbols span:hover {
  border-color: #AFB2C3;
  background-color: #F3F3F7;
}
.modal .modal-bg {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 100;
}

.checkbox {
  position: relative;
}

.custom-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
.custom-checkbox + label {
  display: flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
  color: #414751;
  transition: 0.3s ease-in-out all;
}
.custom-checkbox + label:before {
  content: "";
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid #414751;
  margin-right: 15px;
  font-size: 12px;
  color: #0072FF;
}
.custom-checkbox:checked + label:before {
  border-color: #0072FF;
  font-family: "icomoon" !important;
  content: "\e92b";
}

@keyframes gradient {
  0% {
    transform: translateX(-100%) skew(45deg);
  }
  100% {
    transform: translateX(100%) skew(45deg);
  }
}
