  .html-editor .toolbar .toolbar-row .toolbar-box:nth-child(3) {
    order: -3;
  }
  .html-editor .toolbar .toolbar-row .toolbar-box:nth-child(4) {
    order: -2;
  }
  .html-editor .toolbar .toolbar-row .toolbar-box:nth-child(6) {
